import React from "react";
import Dialog from "@material-ui/core/Dialog";
// import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import CreateNewFolderIcon from "@material-ui/icons/CreateNewFolder";
import Slide from "@material-ui/core/Slide";
import {
  FacebookLoginButton,
  // TwitterLoginButton,
  InstagramLoginButton,
  // LinkedInLoginButton,
} from "react-social-login-buttons";
import "./CreateAccountModal.css";
const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction='up' ref={ref} {...props} />;
});

export default function CreateAccountModal() {
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      <div className='account-add-btn' onClick={handleClickOpen}>
        <CreateNewFolderIcon fontSize='large' color='primary' />
        <span>Add Account</span>
      </div>
      <Dialog
        open={open}
        TransitionComponent={Transition}
        keepMounted
        onClose={handleClose}
        aria-labelledby='alert-dialog-slide-title'
        aria-describedby='alert-dialog-slide-description'
      >
        <DialogTitle id='alert-dialog-slide-title'>
          {"Add New Account"}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id='alert-dialog-slide-description'>
            <FacebookLoginButton onClick={() => alert("Facebook")} />
            <InstagramLoginButton onClick={() => alert("Instagram")} />
            {/* <TwitterLoginButton onClick={() => alert("Twitter")} />
            <LinkedInLoginButton onClick={() => alert("Hello")} /> */}
          </DialogContentText>
        </DialogContent>
        {/* <DialogActions>
          <Button onClick={handleClose} color='primary'>
            Disagree
          </Button>
          <Button onClick={handleClose} color='primary'>
            Agree
          </Button>
        </DialogActions> */}
      </Dialog>
    </div>
  );
}
