import React, { useState, useEffect } from "react";
import { connect } from "react-redux";
import Layout from "../../components/Layout/Layout";
import { Row, Col } from "react-bootstrap";
import { userChangePassword } from "../../actions/auth";
import Button from "@material-ui/core/Button";
import { Snackbar } from "@material-ui/core/";
import Alert from "@material-ui/lab/Alert";
import ManageAccountsDetails from "../../components/ManageAccountsDetails/ManageAccountsDetails";
import ManageAccountForm from "../../components/ManageAccountForm/ManageAccountForm";
import "react-phone-input-2/lib/bootstrap.css";
import ChangePassword from "../../components/ChangePassword/ChangePassword";
import { updateUserProfile } from "../../actions/profile";
const ManageAccount = (props) => {
  const { auth, profile } = props;
  const { inProgress, error, success, user } = auth;
  const [updateData, setUpdateData] = useState(null);
  const [changePassword, setChangePassword] = useState(null);
  const [userProfileImage, setUserProfileImage] = useState(null);
  const [successSnackBarOpen, setSuccessSnackBarOpen] = useState(false);
  useEffect(() => {
    if (changePassword !== null) {
      props.dispatch(userChangePassword(changePassword));
    }
    // eslint-disable-next-line
  }, [changePassword]);
  useEffect(() => {
    if (profile.success) {
      setSuccessSnackBarOpen(true);
    }
  }, [profile.success]);

  const handleSnackBarClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setSuccessSnackBarOpen(false);
  };
  const handleUpdateProfile = () => {
    if (updateData !== null && userProfileImage !== null) {
      props.dispatch(
        updateUserProfile({
          ...updateData,
          user_picture: userProfileImage.target.files[0],
        })
      );
    } else {
      props.dispatch(
        updateUserProfile({
          ...updateData,
        })
      );
    }
  };
  return (
    <div>
      <Layout title='Manage Account' header='Manage Account'>
        <Row>
          <Col sm={12} md={12} lg={4}>
            <ManageAccountsDetails
              user={user}
              userProfileImage={userProfileImage}
              setUserProfileImage={setUserProfileImage}
            />
            <ChangePassword
              setChangePassword={setChangePassword}
              inProgress={inProgress}
              error={error}
              success={success}
            />
          </Col>
          <Col>
            <ManageAccountForm user={user} setUpdateData={setUpdateData} />
            <Button
              variant='contained'
              color='secondary'
              onClick={handleUpdateProfile}
            >
              Update
            </Button>
          </Col>
        </Row>
        <Snackbar
          anchorOrigin={{ vertical: "bottom", horizontal: "center" }}
          open={successSnackBarOpen}
          autoHideDuration={6000}
          onClose={handleSnackBarClose}
        >
          <Alert onClose={handleSnackBarClose} severity='success'>
            Profile Updated Succesfully
          </Alert>
        </Snackbar>
      </Layout>
    </div>
  );
};

const mapStateToProps = (state) => ({
  auth: state.auth,
  profile: state.profile,
});
export default connect(mapStateToProps)(ManageAccount);
