import React, { useState, useEffect } from "react";
import Layout from "../../components/Layout/Layout";
import { connect } from "react-redux";
import { Button, Paper, Snackbar } from "@material-ui/core";
import Alert from "@material-ui/lab/Alert";
import SendIcon from "@material-ui/icons/Send";
import SaveIcon from "@material-ui/icons/Save";
import ReactDropZone from "../../components/ReactDropZone/ReactDropZone";
import ReactSummerNote from "../../components/ReactSummerNote/ReactSummerNote";
import ScheduleTimeDialog from "../../components/ScheduleTimeDialog/ScheduleTimeDialog";
import { Col, Container, Row } from "react-bootstrap";
import { createPost } from "../../actions/posts";
import "./CreatePost.css";
const CreatePost = (props) => {
  const { success } = props.posts;

  const [text, setText] = useState("");
  const [subject, setSubject] = useState("");
  const [scheduleTime, setScheduleTime] = useState("");
  const [files, setFiles] = useState([]);
  const [disabled, setDisabled] = useState(true);
  const [scheduleDisable, setScheduleDisable] = useState(true);
  const [openSchedule, setOpenSchedule] = useState(false);
  const [successSnackBarOpen, setSuccessSnackBarOpen] = useState(false);
  useEffect(() => {
    if (success) {
      setSuccessSnackBarOpen(true);
    }
  }, [success]);
  useEffect(() => {
    if (!!text && !!subject && !!files.length > 0) {
      setDisabled(false);
    } else {
      setDisabled(true);
    }
  }, [text, subject, files]);

  useEffect(() => {
    if (!!scheduleTime) {
      setScheduleDisable(false);
    } else {
      setScheduleDisable(true);
    }
  }, [scheduleTime]);
  const handleScheduleOpen = () => {
    setOpenSchedule(true);
  };

  const handleScheduleClose = () => {
    setOpenSchedule(false);
  };
  const handleSnackBarClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setSuccessSnackBarOpen(false);
  };
  // Submit handlers
  const handleDraft = () => {
    let record = {
      title: subject,
      caption: text,
      post_scheduled: new Date().toISOString(),
      status: "draft",
      image: files[0],
    };
    props.dispatch(createPost(record));
    handleClearState();
  };
  const handlePost = () => {
    let record = {
      title: subject,
      caption: text,
      post_scheduled: new Date().toISOString(),
      status: "post",
      image: files[0],
    };
    props.dispatch(createPost(record));
    handleClearState();
  };
  const handleSchedule = () => {
    let record = {
      title: subject,
      caption: text,
      post_scheduled: scheduleTime,
      status: "schedule",
      image: files[0],
    };
    props.dispatch(createPost(record));
    handleClearState();
    handleScheduleClose();
  };
  const handleClearState = () => {
    setText("");
    setSubject("");
    setScheduleTime("");
    setFiles([]);
  };
  return (
    <div>
      <Layout title='Create' header='Create Post'>
        <Container>
          <Row>
            <Col style={{ marginBottom: "10px" }}>
              <Paper>
                <ReactDropZone files={files} setFiles={setFiles} />
              </Paper>
            </Col>
            <Col lg={8}>
              <Paper>
                <ReactSummerNote
                  subject={subject}
                  text={text}
                  setSubject={setSubject}
                  setText={setText}
                />
                <div className='notebtn'>
                  <Button
                    onClick={handleDraft}
                    disabled={disabled}
                    size='small'
                    variant='contained'
                    color='primary'
                    endIcon={<SaveIcon />}
                  >
                    Draft
                  </Button>
                  <ScheduleTimeDialog
                    setScheduleTime={setScheduleTime}
                    handleSchedule={handleSchedule}
                    handleScheduleOpen={handleScheduleOpen}
                    handleScheduleClose={handleScheduleClose}
                    openSchedule={openSchedule}
                    scheduleDisable={scheduleDisable}
                    disabled={disabled}
                  />
                  <Button
                    onClick={handlePost}
                    disabled={disabled}
                    size='small'
                    variant='contained'
                    color='primary'
                    endIcon={<SendIcon />}
                  >
                    Post
                  </Button>
                </div>
              </Paper>
            </Col>
          </Row>
          <Snackbar
            anchorOrigin={{ vertical: "top", horizontal: "right" }}
            open={successSnackBarOpen}
            autoHideDuration={6000}
            onClose={handleSnackBarClose}
          >
            <Alert onClose={handleSnackBarClose} severity='success'>
              {success}
            </Alert>
          </Snackbar>
        </Container>
      </Layout>
    </div>
  );
};

const mapStateToProps = (state) => ({
  posts: state.posts,
});
export default connect(mapStateToProps)(CreatePost);
