import React, { useState } from "react";
import clsx from "clsx";
import { makeStyles } from "@material-ui/core/styles";
import PhoneIcon from "@material-ui/icons/Phone";
import MailIcon from "@material-ui/icons/Mail";
import LocationOnIcon from "@material-ui/icons/LocationOn";
import { connect } from "react-redux";
import {
  Avatar,
  MenuItem,
  Typography,
  Drawer,
  Divider,
} from "@material-ui/core";
import "./ProfileDrawer.css";
const useStyles = makeStyles((theme) => ({
  list: {
    width: "40vw",
    "& > *": {
      margin: theme.spacing(1),
    },
  },
  fullList: {
    width: "auto",
  },
  large: {
    width: theme.spacing(15),
    height: theme.spacing(15),
  },
  drawerTabs: {
    textAlign: "center",
    backgroundColor: "#bd203f",
    padding: "10px",
    color: "#ffe1ef",
    textTransform: "uppercase",
    borderRadius: "5px",
    marginBottom: "0",
  },
}));

function ProfileDrawer(props) {
  const { user } = props.auth;
  const classes = useStyles();
  const [isProfileDrawerOpen, setIsProfileDrawerOpen] = useState(false);

  const toggleDrawer = (open) => (event) => {
    if (
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return;
    }
    setIsProfileDrawerOpen(open);
  };

  const list = () => (
    <div
      className={clsx(classes.list)}
      role='presentation'
      // onClick={toggleDrawer(false)}
      // onKeyDown={toggleDrawer(false)}
    >
      <span
        style={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          backgroundColor: "rgba(0,0,0,0.3)",
        }}
      >
        <Avatar
          alt={user.name && user.name[0]}
          src={`api${user.user_picture}`}
          className={classes.large}
        />
        <Typography>{user.username}</Typography>
      </span>
      <Divider />
      <div className='twoDivContainer'>
        <Typography
          paragraph
          variant='caption'
          className={clsx(classes.drawerTabs)}
          style={{ marginBottom: "0" }}
        >
          {user.name}
        </Typography>
        <Typography
          paragraph
          variant='caption'
          className={clsx(classes.drawerTabs)}
        >
          Gender : {user.gender}
        </Typography>
      </div>

      <Typography
        paragraph
        variant='caption'
        className={clsx(classes.drawerTabs)}
        style={{
          textAlign: "justify",
        }}
      >
        {user.description}
      </Typography>

      <div className='twoDivContainer'>
        <Typography
          paragraph
          variant='caption'
          className={clsx(classes.drawerTabs)}
        >
          <PhoneIcon style={{ color: "rgba(255,255,255,0.8)" }} /> {user.phone}
        </Typography>
        <Typography
          paragraph
          variant='caption'
          className={clsx(classes.drawerTabs)}
        >
          <MailIcon style={{ color: "rgba(255,255,255,0.8)" }} /> {user.email}
        </Typography>
      </div>
      <Typography
        paragraph
        variant='caption'
        style={{ textAlign: "justify" }}
        className={clsx(classes.drawerTabs)}
      >
        <LocationOnIcon style={{ color: "rgba(255,255,255,0.8)" }} />
        {user.address}
      </Typography>
      <Typography
        paragraph
        variant='caption'
        className={clsx(classes.drawerTabs)}
      >
        pincode : {user.pincode}
      </Typography>
      <Typography
        paragraph
        variant='caption'
        className={clsx(classes.drawerTabs)}
      >
        City : {user.city}
      </Typography>
      <Typography
        paragraph
        variant='caption'
        className={clsx(classes.drawerTabs)}
      >
        State : {user.state}
      </Typography>
      <Typography
        paragraph
        variant='caption'
        className={clsx(classes.drawerTabs)}
      >
        Country : {user.country}
      </Typography>
    </div>
  );

  return (
    <div>
      <MenuItem onClick={toggleDrawer(true)}>Profile</MenuItem>
      <Drawer
        anchor='right'
        open={isProfileDrawerOpen}
        onClose={toggleDrawer(false)}
      >
        {list()}
      </Drawer>
    </div>
  );
}
const mapStateToProps = (state) => ({
  auth: state.auth,
});
export default connect(mapStateToProps)(ProfileDrawer);
