import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Avatar, Badge, Typography } from "@material-ui/core";
import CameraAltIcon from "@material-ui/icons/CameraAlt";
import "./ManageAccountDetails.css";
const useStyles = makeStyles((theme) => ({
  large: {
    width: theme.spacing(20),
    height: theme.spacing(20),
  },
}));
const ManageAccountsDetails = ({
  user,
  setUserProfileImage,
  userProfileImage,
}) => {
  const classes = useStyles();
  return (
    <div>
      <span
        style={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          backgroundColor: "rgba(0,0,0,0.3)",
        }}
      >
        <Badge
          overlap='circle'
          anchorOrigin={{
            vertical: "bottom",
            horizontal: "right",
          }}
          badgeContent={
            <span style={{ position: "relative" }}>
              <CameraAltIcon color='secondary' />
              <input
                className='custom-image-input'
                type='file'
                onChange={(e) => setUserProfileImage(e)}
              />
            </span>
          }
        >
          <Avatar
            alt={user.username && user.username[0]}
            src={
              userProfileImage !== null
                ? URL.createObjectURL(userProfileImage.target.files[0])
                : user.user_picture &&
                  `api${user.user_picture}`
            }
            className={classes.large}
          />
        </Badge>
        <Typography>{user.username}</Typography>
      </span>
      <Typography paragraph variant='h6'>
        Description
      </Typography>
      <Typography
        paragraph
        style={{
          textAlign: "justify",
        }}
      >
        {user.description}
      </Typography>
    </div>
  );
};

export default ManageAccountsDetails;
